import React, { useState, useEffect, Suspense } from "react";
import { Routes, Route, Navigate } from "react-router-dom";
import {
  onAuthStateChanged,
  setPersistence,
  browserLocalPersistence,
} from "firebase/auth";
import { auth, db } from "./firebase";
import { doc, getDoc } from "firebase/firestore";
import CircularProgress from "@mui/material/CircularProgress";
import Footer from "./components/Footer";

const Login = React.lazy(() => import("./components/Login"));
const Signup = React.lazy(() => import("./components/Signup"));
const ManagerDashboard = React.lazy(() => import("./pages/ManagerDashboard"));
const EmployeeDashboard = React.lazy(() => import("./pages/EmployeeDashboard"));
const ServiceManDashboard = React.lazy(() =>
  import("./pages/ServicemanDashboard")
);
const StockPage = React.lazy(() => import("./pages/StockPage"));
const StockOutPage = React.lazy(() => import("./pages/StockOutPage"));
const SalesProgressPage = React.lazy(() => import("./pages/SalesProgressPage"));
const CustomerServiceStatus = React.lazy(() =>
  import("./pages/CustomerServiceStatus")
);
const NotFound = React.lazy(() => import("./components/NotFound"));

function App() {
  const [user, setUser] = useState(null);
  const [role, setRole] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    setPersistence(auth, browserLocalPersistence)
      .then(() => {
        console.log("Auth persistence set to LOCAL.");
      })
      .catch((error) => {
        console.error("Error setting auth persistence:", error);
      });

    const unsubscribe = onAuthStateChanged(auth, async (currentUser) => {
      if (currentUser) {
        setUser(currentUser);
        try {
          const userDoc = await getDoc(doc(db, "users", currentUser.uid));
          if (userDoc.exists()) {
            setRole(userDoc.data().role);
          } else {
            console.error("No user role found in Firestore.");
          }
        } catch (error) {
          console.error("Error fetching user role:", error);
        }
      } else {
        setUser(null);
        setRole(null);
      }
      setLoading(false);
    });

    return () => unsubscribe();
  }, []);

  if (loading) {
    return (
      <div style={{ textAlign: "center", marginTop: "50px" }}>
        <CircularProgress />
      </div>
    );
  }

  const ProtectedRoute = ({ roleRequired, children }) => {
    if (!user) {
      return <Navigate to="/login" />;
    }

    if (role !== roleRequired) {
      if (role === "manager") return <Navigate to="/manager-dashboard" />;
      if (role === "employee") return <Navigate to="/employee-dashboard" />;
      if (role === "serviceMan")
        return <Navigate to="/service-man-dashboard" />;
      return <Navigate to="/login" />;
    }

    return children;
  };

  const PublicRoute = ({ children }) => {
    if (user) {
      if (role === "manager") return <Navigate to="/manager-dashboard" />;
      if (role === "employee") return <Navigate to="/employee-dashboard" />;
      if (role === "serviceMan")
        return <Navigate to="/service-man-dashboard" />;
    }

    return children;
  };

  return (
    <>
      <Suspense
        fallback={
          <div style={{ textAlign: "center", marginTop: "50px" }}>
            <CircularProgress />
          </div>
        }
      >
        <Routes>
          <Route
            path="/signup"
            element={
              <PublicRoute>
                <Signup />
              </PublicRoute>
            }
          />
          <Route
            path="/login"
            element={
              <PublicRoute>
                <Login setUser={setUser} setRole={setRole} />
              </PublicRoute>
            }
          />
          <Route
            path="/manager-dashboard"
            element={
              <ProtectedRoute roleRequired="manager">
                <ManagerDashboard />
              </ProtectedRoute>
            }
          />
          <Route
            path="/stock"
            element={
              <ProtectedRoute roleRequired="manager">
                <StockPage />
              </ProtectedRoute>
            }
          />
          <Route
            path="/stockOut"
            element={
              <ProtectedRoute roleRequired="manager">
                <StockOutPage />
              </ProtectedRoute>
            }
          />
          <Route
            path="/salesProgress"
            element={
              <ProtectedRoute roleRequired="manager">
                <SalesProgressPage />
              </ProtectedRoute>
            }
          />
          <Route
            path="/employee-dashboard"
            element={
              <ProtectedRoute roleRequired="employee">
                <EmployeeDashboard />
              </ProtectedRoute>
            }
          />
          <Route
            path="/service-man-dashboard"
            element={
              <ProtectedRoute roleRequired="serviceMan">
                <ServiceManDashboard />
              </ProtectedRoute>
            }
          />
          <Route path="/check-service-status" element={<CustomerServiceStatus />} />
          <Route path="*" element={<NotFound />} />
          <Route path="/" element={<Navigate to="/login" />} />
        </Routes>
      </Suspense>
      <Footer /> 
    </>
  );
}

export default App;
